import React, { useState, useEffect } from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';


const HeaderBlock = styled.div`
    position:relative;
    position:fixed; top:0; left:0; width:100%; z-index:99;
    .row {
        display:flex; 
        justify-content:space-between;
        align-items:center;
        .logo { 
            img { width:70px; }
         }
        .nav {
            .depth1 { display:flex;
                li { margin:0 20px; padding:20px 0; 
                    .depth2 { display:none;
                        position:absolute; top:100%; left:0%;
                        width:100%; background:#fff; text-align:center;
                        padding:20px 30px; font-size:18px;
                        li { color:#000; padding:5px 0; display:inline-block;}
                    }
                &:hover { border-bottom:2px solid #000; }
                &:hover > .depth2 { display:block }
                }
            }
        }
        .mobileNav {
            .mobileNavList {
                ul {
                    li { display:inline-block; }
                    li:nth-child(1) { margin-right:20px; }
                }
            }
        }
    }
`;

const Header = ({onSelect}) => {

    const [scy, setScy] = useState(0)

    const onScroll = () => {
        setScy(window.scrollY)
    }

    useEffect( ()=>{
        window.addEventListener("scroll", onScroll)
        return () => window.removeEventListener("scroll", onScroll)
    }, [])

    return (
        <HeaderBlock style={{ background: (scy) ? "#fff" : "#fff", color: (scy) ? "#000" : "#000" }}>
            <div className="row">
                <h1 className="logo">
                    <Link to="/">
                        <img src="images/logo.png" alt="" />
                    </Link>
                </h1>
                <nav className="nav">
                    <ul className="depth1">
                        <li><Link to="/men">Men</Link>
                            <ul className="depth2">
                                <li><Link to="/men" onClick={()=>onSelect(0)}>신발</Link></li>
                                <li><Link to="/men" onClick={()=>onSelect(1)}>의류</Link></li>
                                <li><Link to="/men" onClick={()=>onSelect(2)}>용품</Link></li>
                            </ul>
                        </li>
                        <li><Link to="/women">Women</Link>
                            <ul className="depth2">
                                <li><Link to="/women">신발</Link></li>
                                <li><Link to="/women">의류</Link></li>
                                <li><Link to="/women">용품</Link></li>
                            </ul>
                        </li>
                        <li><Link to="/kids">Kids</Link>
                            <ul className="depth2">
                                <li><Link to="/kids">신발</Link></li>
                                <li><Link to="/kids">남아 의류</Link></li>
                                <li><Link to="/kids">여아 의류</Link></li>
                            </ul>
                        </li>
                        {/* <li><Link to="/sale">Sale</Link>
                            <ul className="depth2">
                                <li><Link to="/sale">MEN</Link></li>
                                <li><Link to="/sale">WOMEN</Link></li>
                                <li><Link to="/sale">KIDS</Link></li>
                            </ul>
                        </li> */}
                    </ul>
                </nav>
                <div className="mobileNav">
                    <div className="mobileNavList">
                        <ul>
                            <li><Link to="/login">Login</Link></li>
                            <li><Link to="/join">Join</Link></li>
                        </ul>
                    </div>
                </div>
            </div>
        </HeaderBlock>
    );
};

export default Header;