import React from 'react';
import styled from 'styled-components';

const KidsItemBlock = styled.div`
    width:32.33%;
    margin:0 0.5% 100px;
    img { width:100% }
    div { padding-right:5px;
        margin-top:10px; font-size:16px; color:#999;
        .line1 {
            color:#000; display:flex;
            justify-content: space-between;
        }
    }
`;

const KidsItem = ({name, price, about, color, photo}) => {
    return (
        <KidsItemBlock>
            <img src={photo} alt={about} />
            <div>
                <div className="line1">
                    <p>{name}</p>
                    <p>{price} 원</p>
                </div>
                <p>{about}</p>
                <p>{color}</p>
            </div>
        </KidsItemBlock>
    );
};

export default KidsItem;