import React from 'react';
import './Footer.scss';
import styled from 'styled-components';
import { FaFacebookF, FaYoutube, FaInstagram, FaTwitter } from "react-icons/fa";

const FooterBlock = styled.div`
    background:#000; color:#fff;
    font-size:13px; padding:60px 0;
    .row { max-width:800px; display:flex; justify-content:space-between;
        li { line-height:2em; cursor:pointer; }
        .box_2, .box_3, .box_4 { li + li { color:#999; font-size:12px; }
    }
`;

const Footer = () => {
    return (
        <FooterBlock>
            <div className="row">
                <div className="box_1">
                    <ul>
                        <li>매장안내</li>
                        <li>나이키 저널</li>
                        <li>로그인</li>
                        <li>멤버 가입</li>
                    </ul>
                </div>
                <div className="box_2">
                    <ul>
                        <li>고객센터</li>
                        <li>080-022-0182</li>
                        <li>주문/결제</li>
                        <li>배송</li>
                        <li>주문배송조회</li>
                        <li>멤버쉽 혜택/서비스</li>
                        <li>공지사항</li>
                        <li>1:1 채팅 문의</li>
                        <li>1:1 이메일문의</li>
                        <li>이용약관</li>
                        <li>개인정보처리방침</li>
                    </ul>
                </div>
                <div className="box_3">
                    <ul>
                        <li>ABOUT NIKE</li>
                        <li>나이키에 대하여</li>
                    </ul>
                </div>
                <div className="box_4">
                    <ul>
                        <li>SOCIAL</li>
                        <li className="icons">
                            <p><FaTwitter /></p>
                            <p><FaFacebookF /></p>
                            <p><FaYoutube /></p>
                            <p><FaInstagram /></p>
                        </li>
                    </ul>
                </div>
            </div>
        </FooterBlock>
    );
};

export default Footer;