import React from 'react';
import './Homesection.scss';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import styled from 'styled-components';

const Article1 = styled.div`
    position:relative;
    .slide { position:relative;
        margin-top:60px;
        height:60vw !important;
        background-size:cover;
        background-repeat:no-repeat;
        background-position:center;
        &.slide1 {background-image:url('images/main_1.jpg')};
        &.slide2 {background-image:url('images/main_2.jpg')};
        &.slide3 {background-image:url('images/main_3.jpg')};
    }

    .slick-arrow { position:absolute; top:50%; transform:translateY(-50%);
        background:none; padding:10px; z-index:9; color:red; font-size:20px;
       &.slick-prev { left:20px; }
       &.slick-next { right:20px; }    
    }

`;

const Homesection = () => {
    const settings = {
        dots:false,
        infinite:true,
        speed:600,
        autoplay:true,
        autoplaySpeed:4000,
        slidesToShow:1,
        slidesToScroll:1,
        prevArrow: false,
        nextArrow: false
    }
    return (
        <div className="homeWrap">
            <Article1 className="article1 row">
                <Slider {...settings}>
                    <div className="slide slide1"></div>
                    <div className="slide slide2"></div>
                    <div className="slide slide3"></div>
                </Slider>
                <div className="textBox_1">
                    <h2>PLAY NEW</h2>
                    <div className="text">
                        새로운 도전이 늘 멋진 것만은 아니죠. 넘어지는 게 대부분이니까. 하지만 상관없어요.<br />
                        두 번, 세 번, 네 번, 몇 번이고 계속 도전해보세요.<br />
                        그리고 실패하는 것을 두려워하지 말고 도전하는 자신을 사랑해보세요.<br />
                    </div>
                    <button>나이키와 함께하기</button>
                    <button>구매하기</button>
                </div>
            </Article1>
            <div className="article2">
                <div className="row">
                    <div className="main">
                        <img src="./images/art_2.gif" alt="" />
                    </div>
                    <div className="textBox_2">
                        <h2>나이키 코르크 팩</h2>
                        <div className="text">
                            재생 코르크와 식물 자수 포인트로 새롭게 돌아온 클래식 아이콘을 만나보세요.
                        </div>
                        <button>컬렉션 구매하기</button>
                    </div>
                </div>    
            </div> 
            <div className="article3">
                <div className="row">
                    <p>이번 주 스타일링</p>
                    <div className="main">
                        <img src="./images/art_3.jpg" alt="" />
                    </div>
                    <div className="textBox_3">
                        <h2>리-이슈 컬렉션으로 완성하는 썸머 레트로 룩</h2>
                        <div className="text">
                            87년생 나이키 보팅 컬렉션의 디자인을 새롭게 해석한 제품과 함께<br />
                            시원한 썸머 레트로 룩을 연출해보세요.
                        </div>
                        <button>구매하기</button>
                        <button>더 많은 스타일링 보기</button>
                    </div>
                </div>
            </div> 
            <div className="article4">
                <div className="row">
                    <div className="main">
                        <img src="./images/art_4.gif" alt="" />
                    </div>
                    <div className="textBox_4">
                        <h2>나이키 와플 원</h2>
                        <div className="text">
                            새로운 클래식으로 스타일의 틀을 깨다.<br />
                            나이키 와플 원으로 새로운 룩을 연출해보세요.
                        </div>
                        <button>구매하기</button>
                    </div>
                </div>    
            </div>
        </div>
    );
};

export default Homesection;