import React, {} from 'react';
import styled, {css} from 'styled-components';
import nikeItems from './nikeItems.json';
import MenItem from './MenItem';

const MenBlock = styled.div`
    border-top:63px solid #fff;
    .tabs { display:flex; justify-content:center; }
    .content { display:flex; flex-wrap:wrap;}
`;

const List = styled.div`
    padding:15px 60px; font-weight:bold; font-size:20px;
    cursor:pointer; margin:17px 0px 50px;
    ${
        props => props.active && css`color:blue; border-bottom:3px solid blue;`
    }
`;

const tabTit = [
    { id:0, title:"신발", part:nikeItems.part1 },
    { id:1, title:"의류", part:nikeItems.part2 },
    { id:2, title:"용품", part:nikeItems.part3 }
]



const Mensection = ({onSelect, activeTab}) => {



    return (
        <MenBlock>
            <div className="row">
                <div className="tabs">
                    {
                        tabTit.map( (data) => <List key={data.id} active={ activeTab===data.id } onClick={ () => onSelect(data.id) }>{data.title}</List> )
                    }
                </div>
                <div className="content">
                    {
                        tabTit[activeTab].part.map( (data, index) => <MenItem key={index} name={data.name} price={data.price} about={data.about} color={data.color} photo={data.photo} /> )
                    }
                </div>
            </div>
        </MenBlock>
    );
};

export default Mensection;