import React, {useState, useCallback} from 'react';
import Header from './Header';
import Footer from './Footer';
import styled from 'styled-components';

const MemberTempBlock = styled.div`
    border-top:63px solid #fff;
    .row { padding-top:100px; padding-bottom:100px; 
        h2 { text-align:center;
            img { width:70px; }
        }
    }
`;

const MemberTemp = ({children}) => {

    const [activeTab, setActiveTab] = useState(0)

    const onSelect = useCallback(
            (id) => {
                setActiveTab(id)
            } , []
        )

    return (
        <div>
            <Header onSelect={onSelect} activeTab={activeTab}/>
            <MemberTempBlock>
                <div className="row">
                    <h2><img src="../images/logo.png" alt="" /></h2>
                    { children }
                </div>
            </MemberTempBlock>
            <Footer />
        </div>
    );
};

export default MemberTemp;