import React from 'react';
import styled from 'styled-components';
import Button from './Button';
import {Link} from 'react-router-dom';

const MemberFormBlock = styled.div`
    max-width:500px; margin-left:auto; margin-right:auto;
    h3 { font-size:30px; text-align:center; margin:30px 0; }
`;

const StyledInput = styled.input`
    width:100%; padding:15px 10px; border:1px solid #ddd;
    margin-bottom:20px;
`;

const MemberFoot = styled.div`
    text-align:right;
    margin-top:20px;
    a { text-decoration:underline; }
`;

// const Button = styled.button`
//     width:100%;
//     padding:15px 10px;
//     color:#fff;
//     background:#999;
//     border-radius:10px;
//     border:none;
//     margin-top:20px;
//     &:hover { background:#333 }
// `;

const textType = {
    login:'나이키 로그인',
    join:'나이키 멤버 가입'
}

const MemberForm = ({type}) => {
    const text = textType[type]
    // textType.키이름 = textType["키이름"]     textType[변수]
    // textType.join = textType["join"]     textType[type]
    return (
        <MemberFormBlock>
            <h3>{ text }</h3>
            <form>
                {
                    type==='join' && <StyledInput type="text" placeholder="이름" name="username" />
                }
                <StyledInput type="text" placeholder="아이디" name="userid" />
                <StyledInput type="password" placeholder="비밀번호" name="password" />
                {
                    type==='join' && (
                        <>
                        <StyledInput type="password" placeholder="비밀번호학인" name="passok" />
                        <label>성별 : </label> 남 <input type="radio" name="gender" value="M" /> 여 <input type="radio" name="gender" value="W" />
                        </>
                    )
                }
                <Button type="submit">{text}</Button>
            </form>
            <MemberFoot>
                {
                    type==='login' ? <Link to="/join">회원가입</Link> : <Link to="login">로그인</Link>
                }
            </MemberFoot>
        </MemberFormBlock>
    );
};

export default MemberForm;